import React from 'react'
import { BrowserRouter as Router, Route } from 'react-router-dom'

import Main from './page/Main'
import Login from './page/Login'

const Routing = () =>
    (
        <Router>
            <Route exact path='/main' >
                <Main />
            </Route>
            <Route path='/login' >
                <Login />
            </Route>
        </Router>

    )
export default Routing